import { defineCustomElement as defineCustomElementLabel } from '@ionic/core/components/ion-label';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpChartBar } from '../../chart-bar';

defineCustomElementLabel();

const meta: Meta<TpChartBar> = {
  title: 'components/Core/Chart bar/Compositions',
  argTypes: {
    value: {
      control: { type: 'range', min: 0, max: 100, step: 1 },
      description: 'Define o valor da barra de progresso.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: 'number' },
      },
    },
    width: {
      control: { type: 'number' },
      description: 'Define a largura do componente.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: 'number' },
      },
    },
    height: {
      control: { type: 'number', min: 1 },
      description: 'Define a altura do componente.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: 'number' },
      },
    },
  },
  args: {
    value: 0,
    height: 50,
    width: 32,
  },
};

export default meta;

type Story = StoryObj<TpChartBar>;

export const Label: Story = {
  render: ({ ...args }) => html`
    <tp-chart-bar
      color="${args.color}"
      value="${args.value}"
      width="${args.width}"
      height="${args.height}"
    >
      <ion-label tp-type="p12b"> ${args.value}% </ion-label>
    </tp-chart-bar>
  `,
};
